      description:
        'Depois de passar anos caindo na lbia de vendedores, arrecadadores de doaes e operadores de telemarketing, o psiclogo Robert B. Cialdini resolveu se dedicar ao estudo da persuaso. Ele queria entender quais so os fatores que levam uma pessoa a dizer "sim" a um pedido e que tcnicas exploram melhor esses fatores. Reunindo dados das mais recentes pesquisas cientficas sobre o assunto, histrias de gente comum e a experincia adquirida ao se infiltrar em organizaes que treinam os chamados "profissionais da persuaso", Cialdini criou uma obra acessvel, informativa e indispensvel a todos aqueles que querem saber como influenciar pessoas e, ao mesmo tempo, se defender dos manipuladores. Seis princpios psicolgicos bsicos governam o comportamento humano quando tomamos uma deciso e podem ser usados como verdadeiras armas: 1. Reciprocidade: nos sentimos compelidos a retribuir, nem sempre de forma vantajosa para ns, o que outra pessoa nos proporcionou 2. Compromisso e coerncia: depois que fazemos uma escolha, enfrentamos presses para nos comportarmos de maneira condizente com o compromisso assumido 3. Aprovao social: buscamos nos outros indcios do comportamento mais apropriado a seguir 4. Afeio: preferimos acatar pedidos de pessoas que conhecemos e de que gostamos 5. Autoridade: temos um arraigado senso de obedincia  autoridade 6. Escassez: tudo se torna mais valioso quando fica menos disponvel Cada princpio  discutido pelo autor em termos de sua funo na sociedade e de como um profissional da persuaso pode mobilizar seu poder em pedidos de compras, doaes, concesses, votos, permisses, etc. Alm disso, aprendemos a usar essas tcnicas a nosso favor nas interaes dirias de todo tipo, com vizinhos, amigos, colegas, parceiros de negcios ou familiares.',
